













import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component({
  components: {},
})
export default class Problem extends Vue {
  @Prop()
  num?: number | string;
  @Prop()
  problem?: string;
  flag = true;
  goback() {
    if (!this.flag) return;
    this.flag = false;
    setTimeout(() => {
      if (this.num === 1) return;
      this.$router.back();
      this.flag = true;
    }, 1000);
  }
}
